.card-login {
    height: 80%;
    max-height: 450px;
    width: 80%;
    max-width: 600px;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 5%;
    justify-content: center;
    background: #2c323e;
    position: relative;
    padding: 10px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 20px;
  }

.card-create-user {
    height: 80%;
    max-height: 650px;
    width: 80%;
    max-width: 600px;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 5%;
    justify-content: center;
    background: #2c323e;
    position: relative;
    padding: 10px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 20px;
}

.container {
  margin-top: 8%;
  width: 100%;
